.left-enter {-webkit-transform: translate(100%);transform: translate(100%);
}
.left-enter-to {-webkit-transform: none;transform: none;
}
.left-enter-active {transition: 1s;
}
.left-leave {-webkit-transform: none;transform: none;
}
.left-leave-to {-webkit-transform: translate(-100%);transform: translate(-100%);
}
.left-leave-active {transition: 1s;
}
.right-enter {-webkit-transform: translate(-100%);transform: translate(-100%);
}
.right-enter-to {-webkit-transform: none;transform: none;
}
.right-enter-active {transition: 1s;
}
.right-leave {-webkit-transform: none;transform: none;
}
.right-leave-to {-webkit-transform: translate(100%);transform: translate(100%);
}
.right-leave-active {transition: 1s;
}



